import React, { useState, useEffect, useCallback } from "react";
import { useParams, Link } from "react-router-dom";
import api from "../services/api";
import LoadingMessage from "../components/LoadingMessage";
import ErrorMessage from "../components/ErrorMessage";

function StaffDetailPage() {
  const { id } = useParams();
  const [staff, setStaff] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  const fetchStaff = useCallback(async () => {
    setLoading(true);
    try {
      const response = await api.get(`/staffs/${id}`); // URL theo  bn
      setStaff(response.data.data);
      setError("");
    } catch {
      setError("Khng th ti thng tin nhn vin.");
    } finally {
      setLoading(false);
    }
  }, [id]);

  useEffect(() => {
    fetchStaff();
  }, [fetchStaff]);

  if (loading) return <LoadingMessage message="ang ti thng tin nhn vin..." />;
  if (error) return <ErrorMessage message={error} />;
  if (!staff) return <ErrorMessage message="Khng tm thy nhn vin." />;

  const accountDetails = [
    { label: "H v tn", value: staff.full_name },
    { label: "Ngy sinh", value: staff.day_of_birth ? new Date(staff.day_of_birth).toLocaleDateString("vi-VN") : "Cha c" },
    { label: "S CMND/CCCD", value: staff.id_number || "Cha c" },
    { label: "Ngy cp CMND", value: staff.id_issued_date ? new Date(staff.id_issued_date).toLocaleDateString("vi-VN") : "Cha c" },
    { label: "M s thu", value: staff.tax_id || "Cha c" },
    { label: "S in thoi", value: staff.phone_number || "Cha c" },
    { label: "Ti khon ngn hng", value: staff.bank_account || "Cha c" },
    { label: "Tn ngn hng", value: staff.bank_name || "Cha c" },
    { label: "Ngy to", value: staff.created_at ? new Date(staff.created_at).toLocaleDateString("vi-VN") : "Cha c" },
  ];

  return (
    <div className="container">
      <Link to="/staffs" className="btn btn-outline btn-sm link">
        &larr; Quay li danh sch nhn vin
      </Link>

      <div className="detail-page">
        {/* Sidebar */}
        <div className="detail-sidebar">
          <div className="card">
            <div className="card-body">
              <div className="avatar mb-3">{staff.full_name ? staff.full_name[0] : "?"}</div>
              <h3>{staff.full_name}</h3>
            </div>
          </div>

          <div className="card">
            <div className="card-header">Thng tin ti khon</div>
            <div className="card-body">
              {accountDetails.map((item) => (
                <div key={item.label} className="detail-row">
                  <span className="detail-label">{item.label}</span>
                  <span className="detail-value">{item.value}</span>
                </div>
              ))}
            </div>
          </div>

          <div className="card">
            <div className="card-header">nh CMND</div>
            <div className="card-body">
              {staff.image_front && <img src={staff.image_front} alt="CMND mt trc" className="mb-2" />}
              {staff.image_back && <img src={staff.image_back} alt="CMND mt sau" />}
              {!staff.image_front && !staff.image_back && <p>Cha c nh CMND</p>}
            </div>
          </div>
        </div>

        {/* Main Content */}
        <main className="detail-main">
          <div className="card">
            <div className="card-header">Thng tin b sung</div>
            <div className="card-body text-gray-600">
              <p>Cha c d liu b sung.</p>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}

export default StaffDetailPage;
