// src/pages/UserDetailPage.jsx
import React, { useState, useEffect, useCallback } from "react";
import { useParams, Link } from "react-router-dom";
import api from "../services/api";
import LoadingMessage from "../components/LoadingMessage";
import ErrorMessage from "../components/ErrorMessage";

function UserDetailPage() {
  const { id } = useParams();
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  const fetchUser = useCallback(async () => {
    setLoading(true);
    try {
      const response = await api.get(`/users/${id}`);
      setUser(response.data.data);
      setError("");
    } catch {
      setError("Khng th ti thng tin ngi dng.");
    } finally {
      setLoading(false);
    }
  }, [id]);

  useEffect(() => {
    fetchUser();
  }, [fetchUser]);

  if (loading) return <LoadingMessage text="ang ti thng tin ngi dng..." />;
  if (error) return <ErrorMessage text={error} />;
  if (!user) return <ErrorMessage text="Khng tm thy ngi dng." />;

  const accountDetails = [
    { label: "H v tn", value: user.full_name },
    { label: "Email", value: user.email },
    { label: "S in thoi", value: user.phone_number || "Cha c" },
    {
      label: "Trng thi",
      value: (
        <span className={user.is_active ? "badge badge-success" : "badge badge-error"}>
          {user.is_active ? "Hot ng" : "Ngng hot ng"}
        </span>
      ),
    },
    {
      label: "Ngy tham gia",
      value: new Date(user.created_at).toLocaleDateString("vi-VN"),
    },
  ];

  return (
    <div className="container">
      <Link to="/campaigns" className="btn btn-outline btn-sm link">
        &larr; Quay li danh sch chin dch
      </Link>

      <div className="detail-page">
        {/* Sidebar */}
        <div className="detail-sidebar">
          <div className="card">
            <div className="card-body">
              <div className="avatar mb-3">
                {user.full_name ? user.full_name[0] : "?"}
              </div>
              <h3>{user.full_name}</h3>
              <p className="text-muted">{user.role}</p>
            </div>
          </div>

          <div className="card">
            <div className="card-header">Thng tin ti khon</div>
            <div className="card-body">
              {accountDetails.map((item) => (
                <div key={item.label} className="detail-row">
                  <span className="detail-label">{item.label}</span>
                  <span className="detail-value">{item.value}</span>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Main Content */}
        <main className="detail-main">
          <div className="card">
            <div className="card-header">Thng tin b sung</div>
            <div className="card-body text-gray-600">
              <p>Cha c d liu b sung.</p>
              {/* TODO: bng activity, logs, hoc chi tit khc */}
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}

export default UserDetailPage;
